import { typeOf } from "@/components/_utils";
import { FormFieldAttrs } from "@/components/BaseFormItem";
import { CommonObj, OptionItem, StrNum } from "@/vite-env";
import { emptyVals, emptyTime } from "./consts";
import dayjs from "dayjs";
import * as xlsx from "xlsx";

/**
 * excel
 * @param fileBuffer
 * @returns {unknown[]}
 */
export function importExcel(
  fileBuffer: any,
  opts = {
    defval: "", //
  }
): CommonObj[] {
  const workbook = xlsx.read(fileBuffer, { type: "buffer" }); //  workbook
  const firstSheetName = workbook.SheetNames[0]; // 
  const firstSheet = workbook.Sheets[firstSheetName]; // 
  return xlsx.utils.sheet_to_json(firstSheet, opts); // 
}

/**
 * Excel
 * @param data array   [["A1", "B1", "C1"], ["A2", "B2", "C2"],["A3", "B3", "C3"]]   
 * @param {string} fileName 
 * @param {string} sheetName 
 * @param {Function} callback 
 * @link  https://www.jianshu.com/p/f9ba3dd3cd4f
 */
export function exportExcel(
  data: string[][] = [],
  fileName = dayjs().format("YYYYMMDD"),
  sheetName = "1",
  callback?: () => void
) {
  const wb = xlsx.utils.book_new(); // workbook
  // const ws = xlsx.utils.aoa_to_sheet(data); // sheet
  const ws = xlsx.utils.json_to_sheet(data, {
    // header: ["A", "B", "C", "D", "E", "F", "G"],
    skipHeader: true,
  }); // sheet
  xlsx.utils.book_append_sheet(wb, ws, sheetName); // sheetworkbook
  xlsx.writeFile(wb, fileName + ".xls"); // ()
  callback?.();
}

/**
 * ////
 */
export function getTimeAgo(time: any) {
  if (!time) return "";
  time = dayjs().diff(time) / 1000;
  let unit = "";
  let num = 0;
  const y = Math.floor(time / (60 * 60 * 24 * 365));
  if (y > 0) {
    unit = "";
    num = y;
    return `${num}${unit}`;
  } else {
    const M = Math.floor(time / (60 * 60 * 24 * 30));
    if (M > 0) {
      unit = "";
      num = M;
      return `${num}${unit}`;
    } else {
      const d = Math.floor(time / (60 * 60 * 24));
      if (d > 0) {
        unit = "";
        num = d;
      } else {
        const h = Math.floor(time / (60 * 60));
        if (h > 0) {
          unit = "";
          num = h;
        } else {
          const m = Math.floor(time / 60);
          if (m > 0) {
            unit = "";
            num = m;
          } else {
            return "";
          }
        }
      }
      return `${num}${unit}`;
    }
  }
}

/**
 * 
 * @param time new Date()
 * @return string  //  XX XX XX
 */
export function getLastTimeStr(time: any) {
  const lastTime = new Date(time).getTime();
  const diffTime = (Date.now() - lastTime) / 1000;
  if (diffTime < 0) {
    console.error("");
    return "-";
  } else {
    // const M = Math.trunc(diffTime / (30 * 24 * 60 * 60));
    // if (M > 0) {
    //   return `${M}`;
    // } else {
    const d = Math.trunc(diffTime / (24 * 60 * 60));
    if (d > 0) {
      return `${d}`;
    } else {
      const h = Math.trunc(diffTime / (60 * 60));
      if (h > 0) {
        return `${h}`;
      } else {
        const m = Math.trunc(diffTime / 60);
        return m > 0 ? `${m}` : "";
      }
    }
    // }
  }
}

/**
 *  Form echo data
 * @description 
 */
export function handleFormInitData(field: FormFieldAttrs, modelValue?: CommonObj) {
  if (!modelValue || emptyVals.includes(emptyTime)) return;
  const { type, prop } = field;
  const propType = typeOf(prop);
  if (type?.includes("date")) {
    if (propType === "String") {
      if (modelValue[prop as string] === emptyTime) {
        modelValue[prop as string] = "";
      }
    } else if (propType === "Array") {
      (prop as string[]).forEach((key: string) => {
        if (modelValue[key] === emptyTime) {
          modelValue[key] = "";
        }
      });
    }
  }
}

/**
 * 
 * @param {number} total  
 * @param {number} cols   
 * @returns
 */
export function getEmptyNum(total: number, cols: number) {
  return cols - (total % cols);
}

/**
 * order
 * @param opts {}[] 
 * @param inds number[] 
 */
export function getTextFromOpts(opts: OptionItem[] = [], inds: number[] = []) {
  let text = "";
  function cycle(opts: OptionItem[], ind: number = 0) {
    const { label = "", children = [] } = opts?.[inds[ind]] || {};
    text += label;
    if (children?.length) {
      cycle(children, ind + 1);
    }
  }
  cycle(opts);
  return text;
}

/**
 * http
 * @param path {string} 
 * @returns
 */
export const getImgUrl = (path: string) => {
  return new URL(path, import.meta.url).href;
};

/**
 * id
 * @param tree {}[] 
 * @param {string | number} id  id
 * @param key 
 * @param keyMap 
 */
export function getTextFromTreeByKey(
  tree: CommonObj[] = [],
  val: StrNum,
  key: string,
  keyMap: CommonObj = { id: "id", children: "children" }
) {
  if (!val) return "";
  let text = "";
  tree?.find(item => {
    if (item[keyMap.id] == val) {
      text = item[key];
      return !!text;
    } else {
      text = getTextFromTreeByKey(item[keyMap.children], val, key);
      return !!text;
    }
  });
  return text;
}

/**
 * ()optionslabel
 */
export function getLabelFromOptionsByLastValue(
  options: CommonObj[],
  val: StrNum,
  propsMap: CommonObj = {
    label: "label",
    value: "value",
    children: "children",
  },
  emptyChar = "-"
) {
  const { label: labelKey, value: valueKey, children: childrenKey } = propsMap;
  let target: CommonObj | undefined;
  function getLabel(opts: CommonObj[]): boolean {
    return !!opts.find(item => {
      const children = item[childrenKey];
      const value = item[valueKey];
      const isFind = value === val;
      if (isFind) {
        target = item;
      } else {
        if (children?.length) {
          return getLabel(children);
        }
      }
      return isFind;
    });
  }
  getLabel(options);
  return target?.[labelKey] ?? emptyChar;
}

/**
 * optionslabel
 */
export function getLabelFromOptionsByAllValues(
  options: CommonObj[],
  values: StrNum[],
  propsMap: CommonObj = {
    label: "label",
    value: "value",
    children: "children",
  },
  char = ""
) {
  const { label: labelKey, value: valueKey, children: childrenKey } = propsMap;
  const labels: string[] = [];
  function getLabel(opts: CommonObj[], level = 0) {
    opts.find(item => {
      const children = item[childrenKey];
      const value = item[valueKey];
      const label = item[labelKey];
      const isFind = value === values[level];
      if (isFind) {
        labels.push(label);
        if (children) {
          getLabel(children, ++level);
        }
      }
      return isFind;
    });
  }
  getLabel(options);
  return labels.join(char);
}

/**
 * url
 * @param {string} url  
 * @param {string} name  
 */
export function downloadByUrl(url: string, name: string = dayjs().format("YYYY-MM-DD")) {
  const a = document.createElement("a");
  a.download = name;
  a.href = url;
  a.style.display = "none";
  a.click();
  a.remove();
}

/**
 *  bufferData 
 * @param {string} buffer  Buffer
 * @param {string} name  
 */
export function downloadByBuffer(buffer, name?: string) {
  const blob = new Blob([buffer], { type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet" });
  const url = window.URL.createObjectURL(blob);
  downloadByUrl(url, name);
}

/**
 * @description 
 * @returns {String}
 */
export function getBrowserLang() {
  const lang = (navigator.language ?? navigator.browserLanguage).toLowerCase();
  return ["cn", "zh", "zh-cn"].includes(lang) ? "zh" : "en";
}
