import { FormField } from "@/components/BaseFormItem";
import BaseForm from "@/components/form/BaseForm";
import { getUserInfo } from "@/utils";
import { CommonObj } from "@/vite-env";
import { useEffect, useState } from "react";

interface Props {
  id: number;
}
const fields: FormField[] = [
  {
    name: "avatar",
    label: "",
    type: "UploadAvatar",
  },
  {
    name: "nickname",
    label: "",
    attrs: {
      maxLength: 6,
    },
  },
  {
    name: "name",
    label: "",
    attrs: {
      maxLength: 6,
    },
  },
  {
    name: "gender",
    label: "",
    type: "RadioGroup",
    attrs: {
      options: "Gender",
    },
  },
  {
    name: "age",
    label: "",
    type: "InputNumber",
    attrs: {
      maxLength: 2,
      max: 99,
      min: 0,
    },
    otherAttrs: {
      valid: "age",
    },
  },
  {
    name: "type_text",
    label: "",
    attrs: {
      disabled: true,
    },
  },
  {
    name: "address",
    label: "",
    type: "Cascader",
    attrs: {
      options: "Region",
    },
  },
  {
    name: "phone",
    label: "",
    otherAttrs: {
      valid: "phone",
    },
  },
  {
    name: "password",
    label: "",
    otherAttrs: {
      valid: "password",
    },
  },
  {
    name: "signature",
    label: "",
    type: "TextArea",
    attrs: {
      maxLength: 50,
    },
  },
];
export default ({ id }: Props) => {
  useEffect(() => {
    getDetail(id);
  }, []);
  const [initVals, setInitVals] = useState<CommonObj | null>(null);
  function getDetail(id: number) {
    setTimeout(() => {
      setInitVals(getUserInfo());
    }, 500);
  }
  return (
    <>
      {/* <BaseAvatar size={120} className="ml-a mr-a"  round/> */}
      {initVals && <BaseForm initialValues={initVals} style={{ width: "450px" }} fields={fields} />}
    </>
  );
};
