import SectionForm, { SectionFormField } from "@/components/form/SectionForm";
import { name, version, scripts, dependencies, devDependencies } from "#/package.json";
import { FormField } from "@/components/BaseFormItem";
import { CommonObj } from "@/vite-env";

const sections: SectionFormField[] = [
  {
    title: "",
    fields: [
      { name: "name", label: "" },
      { name: "version", label: "" },
      {
        name: "env",
        label: "",
      },
      {
        name: "desc",
        label: "",
      },
    ],
  },
  {
    title: "",
    fields: toFieldItem(scripts),
  },
  {
    title: "",
    fields: toFieldItem(dependencies),
  },
  {
    title: "",
    fields: toFieldItem(devDependencies),
  },
];
const env = import.meta.env.MODE;
const envMap: CommonObj = {
  development: "",
  production: "",
  test: "",
};
function toFieldItem(obj: CommonObj) {
  const arr: FormField[] = [];
  for (let key in obj) {
    arr.push({ name: key, label: key });
  }
  return arr;
}
export default () => {
  const inivVals = {
    name,
    version,
    env: `${envMap[env]}${env}`,
    desc: "",
    ...scripts,
    ...dependencies,
    ...devDependencies,
  };
  return <SectionForm style={{ width: "500px" }} initialValues={inivVals} sections={sections} pureText />;
};
