import { useRoutes, Navigate, useLocation } from "react-router-dom";
import React, { ReactNode, useEffect, useState } from "react";
import Layout from "@/layout";
import { ResponseMenuItem } from "@/layout/_types";
import { CommonObj } from "@/vite-env";
import { useRouter, useStoreSlice } from "@/hooks";
import { camelCase } from "lodash";
import Login from "@/views/login";
import { getRouteIsAuth } from "./_hooks/router";

export interface RouteItem {
  name: string; // 
  path: string;
  element?: ReactNode;
  children?: RouteItem[];
  meta?: {
    title?: string;
    cache?: boolean; // 
    auth?: boolean; // 
  };
}

const modules: CommonObj = import.meta.glob("../views/**/**.tsx");

export const LazyLoad = (path: string) => {
  /**
   * const Module = React.lazy(() => import(`..${path}`));
   * import.meta.glob
   * */
  const Module = React.lazy(modules[`../views${path}`]);
  return <Module />;
};

//allMenusroutes
function getFlatRoutes(menus: ResponseMenuItem[]): RouteItem[] {
  const routes: RouteItem[] = [];
  menus.forEach((menu: ResponseMenuItem) => {
    const { children } = menu;
    children?.forEach(child => pushRoutes(child));
    function pushRoutes(menu: ResponseMenuItem) {
      const { label, path, component, children, link_type } = menu;
      const route: RouteItem = {
        name: link_type ? "" : camelCase(path), //
        path,
      };
      if (children?.length) {
        children.forEach(child => pushRoutes(child));
      } else {
        route.element = LazyLoad(component as string);
        route.meta = {
          title: label,
          // cache: true,
          // auth: true,
        };
        routes.push(route);
      }
    }
  });
  return routes;
}

const errorRoutes = [
  {
    path: "/403",
    name: "noAuth",
    element: LazyLoad("/error/403.tsx"),
  },
  {
    path: "*",
    name: "notFound",
    element: LazyLoad("/error/404.tsx"),
  },
  {
    path: "/500",
    name: "serverError",
    element: LazyLoad("/error/500.tsx"),
  },
];

