export default () => {
  const { allMenus } = useStoreSlice("menu");
  const { updateRoutesState } = useStoreSlice("routes");
  const router = useRouter();
  const location = useLocation();
  const [routes, setRoutes] = useState<RouteItem[]>([
    {
      name: "root",
      path: "/",
      element: <Layout />,
      // element: <Navigate to="home" />,
      children: [],
    },
    // { path: "/login", element: LazyLoad("/views/login/index.tsx") }, //
    { name: "login", path: "/login", element: <Login /> },
    // ...errorRoutes,
  ]);

  useEffect(() => {
    createRoutes(allMenus);
  }, [allMenus]);
  function createRoutes(menus: ResponseMenuItem[]) {
    routes[0].children = [
      { path: "", name: "home", element: LazyLoad("/home/index.tsx") },
      ...getFlatRoutes(menus),
      ...errorRoutes,
    ];
    const newRoutes = [...routes];
    //
    setRoutes(newRoutes);
    setTimeout(() => {
      updateRoutesState({ isCreatedRoute: true });
      // const { pathname } = location;
      // const isAuth = getRouteIsAuth(pathname);
      // if (!isAuth) router.push(`/403?redirect=${pathname}`);
    });
  }
  return useRoutes(routes);
};
