import axios, { AxiosResponse, AxiosRequestConfig } from "axios";
import { showMessage, storage, typeOf } from "@/utils";
import qs from "qs";
import { CommonObj, SetTimeout, TostMessageType } from "@/vite-env";
// import { useRouter } from "vue-router";

type ToastType = "warning" | "info" | "success" | "error";

let historyMsg = ""; // 
let timer: SetTimeout = null;
let showLoadTimer: SetTimeout = null;
let loading: null | CommonObj = null;
let loadingCount = 0;
// const router = useRouter();
const startLoading = () => {
  // loading = ElLoading.service({
  //   lock: true,
  //   text: "",
  //   background: "rgba(0, 0, 0, 0.5)",
  // });
};
const endLoading = () => {
  loading?.close();
  loading = null;
};
const showLoading = () => {
  if (loadingCount === 0) {
    showLoadTimer = setTimeout(() => {
      startLoading();
      showLoadTimer = null;
    }, 1000);
  }
  loadingCount += 1;
};

const hideLoading = () => {
  if (loadingCount > 0) {
    loadingCount -= 1;
    if (loadingCount === 0) {
      if (showLoadTimer) {
        clearTimeout(showLoadTimer);
        showLoadTimer = null;
      } else {
        endLoading();
      }
    }
  }
};
const service = axios.create({ timeout: 6000 });
const isDev = process.env.NODE_ENV === "development";
function showToast(msg: string, type: TostMessageType = "warning") {
  if (historyMsg === msg) {
    historyMsg = "";
    return;
  }
  historyMsg = msg;
  timer && clearTimeout(timer);
  timer = setTimeout(() => {
    historyMsg = "";
    timer && clearTimeout(timer);
  }, 300);
  showMessage(msg, type);
}

service.interceptors.request.use(
  (cfg: AxiosRequestConfig) => {
    showLoading();
    cfg.headers["Content-Type"] = "application/json;charset=utf-8";
    cfg.headers["token"] = storage.getItem("token");
    cfg.headers["device"] = storage.getItem("user")?.device;
    return cfg;
  },
  (err: CommonObj) => {
    console.error("", err);
    return err;
  }
);

service.interceptors.response.use(
  (res: AxiosResponse) => {
    const { data: resData } = res;
    const { code, data, msg } = resData;
    hideLoading();
    if (code === 0) {
      return data;
    } else if (code === 401) {
      showToast("token");
      const { pathname, search } = location;
      location.href = `/login?redirect=${pathname}${search}`;
      return;
    } else {
      // const isDevTips = code > 0; //
      // if (isDevTips) {
      showToast(msg === undefined ? "" : msg, "error");
      // }
      console.error("", msg);
      throw new Error(msg);
    }
  },
  (err: CommonObj) => {
    const { message, config } = err;
    hideLoading();
    if (message === "Network Error") {
      showToast(useMock ? "npm run mockmock" : "", "error");
    } else if (message.includes("404")) {
      console.error("" + config.url);
      showToast("");
    } else if (message.includes("timeout")) {
      showToast("");
    } else {
      console.error("", err);
      showToast("");
    }
    return Promise.reject(err);
  }
);

/**
 * http
 * @param {String} method get,post,put,patch,delete
 * get 
 * post 
 * put post
 * patch postputpatch
 * delete 
 * @param {String} url 
 * @param {Object} data 
 * @param {Object} others axiosaxios
 * @returns
 */
export default (method = "get", url = "", data: any, others = {}, isStringify = false) => {
  method = method.toLowerCase();
  // if (!url.startsWith("http")) {
  //   url = baseUrl + url;
  // }
  const cfg: CommonObj = { url, method, ...others };
  if (["get"].includes(method)) {
    cfg.params = data;
  } else if (["post", "put", "patch", "delete"].includes(method)) {
    // cfg.data = isStringify ? qs.stringify(data) : data;
    const isObj = typeOf(data) === "Object";
    if (isObj) {
      cfg.data = isStringify ? qs.stringify(data) : data;
    } else {
      cfg.data = data;
    }
    // cfg.data = isObj && isStringify ? qs.stringify(data) : data;
  } else {
    throw new Error("");
  }
  return service(cfg);
};
