// 
export const defaultValidTypes: CommonObj = {
  // 
  phone: {
    rules: [
      {
        pattern: regexp.phone,
        message: "11",
      },
    ],
    attrs: {
      maxLength: 11,
    },
  },
  // 
  password: {
    rules: [
      {
        min: 6,
        message: "6",
      },
      {
        pattern: regexp.password,
        message: "6~16 + ",
      },
    ],
    attrs: {
      type: "password",
      maxLength: 16,
    },
  },
  // 
  userName: {
    rules: [
      {
        min: 2,
        message: "",
      },
    ],
    attrs: {
      maxLength: 5,
    },
  },
  // 
  height: {
    type: "InputNumber",
    attrs: {
      min: 0,
      max: 200,
      addonAfter: "cm",
    },
  },
  // 
  age: {
    type: "InputNumber",
    attrs: {
      min: 0,
      max: 100,
      addonAfter: "",
    },
  },
  // 
  weight: {
    type: "InputNumber",
    attrs: {
      min: 0,
      max: 180,
      addonAfter: "kg",
    },
  },
  // 
  rmb: {
    type: "InputNumber",
    attrs: {
      min: 0,
      step: 0.01,
      addonAfter: "",
    },
  },
};
