import React from "react";
import { ResponseMenuItem } from "@/layout/_types";
import { AntdMenuItem } from "@/layout/_types";
import { createSlice, current } from "@reduxjs/toolkit";
import { getUserInfo, storage } from "@/utils";
import { CommonObj } from "@/vite-env";
import { updateState } from "../_utils";
import * as Icons from "@ant-design/icons";

export function convertToMenuNavs(menus: ResponseMenuItem[] = [], level = 0): AntdMenuItem[] {
  const user = getUserInfo();
  const filterMenus = menus.filter((it: ResponseMenuItem) => {
    const { type, auth_codes } = it;
    if (!auth_codes?.length) return true;
    return type !== 2 && auth_codes.includes(user?.type);
    // return it.type !== 2;
  });
  const antdMenus = filterMenus.map((item: ResponseMenuItem) => {
    let { path, icon = "TwitterOutlined", children, label, type } = item;
    const obj: CommonObj = {
      key: path,
      icon: level < 1 ? React.createElement(Icons[icon] ?? Icons.TwitterOutlined) : null, // 
      label,
      type,
    };
    if (children?.length) {
      obj.children = convertToMenuNavs(children, level + 1);
    }
    return obj as AntdMenuItem;
  });
  return antdMenus;
}

const localMenus = storage.getItem("allMenus") || [];
const menuSlice = createSlice({
  name: "menu",
  initialState: {
    isCollapse: storage.getItem("isCollapse", "session") ?? false, //
    updatedTitle: false, // document.title
    openKeys: [], //
    seledKeys: [], //
    activeIndex: 0, // 
    allMenus: localMenus, // 
    sideMenus: localMenus[0]?.children,
  } as CommonObj,
  reducers: {
    // state
    updateMenuState: updateState,
    // 
    initMenus: (state, { payload = [] }) => {
      state.allMenus = payload;
    },
  },
  selectors: {
    // state: state => state,
    // getActiveKeysTest(state, args) {
    //   console.log(state, args, "args------------");
    //   let openKeys: string[] = [];
    //   let seledKeys: string[] = [];
    //   return { openKeys, seledKeys };
    // },
  },
});

export default menuSlice;
