/**
 *  n  n 
 * @param {Function} fn 
 * @param {Boolean} immediate 
 * @param {Number} delay 
 * @example methods: {onSubmit: debounce((event, param) => {console.log("");})}
 */
export function debounce(fn: (...args: any) => void, immediate = true, delay = 1000) {
  let timer: any = null;
  return function (...args: any) {
    if (timer) clearTimeout(timer);
    if (immediate) {
      const canExe = !timer;
      timer = setTimeout(() => {
        timer = null;
      }, delay);
      if (canExe) {
        fn(...args);
      }
    } else {
      timer = setTimeout(() => {
        fn(...args);
        timer = null;
      }, delay);
    }
  };
}

