/**
 *  n 
 * @param {Function} fn 
 * @param {Boolean} immediate 
 * @param {Number} delay 
 * @example methods: {onSubmit: throttle((event, param) => {console.log("");})}
 */
export function throttle(fn: (...args: any) => void, immediate = true, delay = 1000) {
  if (immediate) {
    let previous = 0;
    return (...args: any) => {
      const now = Date.now();
      if (now - previous > delay) {
        fn(...args);
        previous = now;
      }
    };
  }
  let timer: any = null;
  return (...args: any) => {
    if (!timer) {
      timer = setTimeout(() => {
        fn(...args);
        timer = null;
      }, delay);
    }
  };
}

