import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";

//  createAsyncThunk  Action 
export const getAsyncCountAction = createAsyncThunk(
  /**
   *  action  Redux  action
   * 
   */
  "getBannerAction",
  /**
   * 
   *  thunk  payload
   * thunk
   * dispatch
   *  dispatch  action 
   * getState
   *  Redux store  state 
   * extra
   * 
   * @returns  extraReducers  action.payload 
   */
  async (payload, { dispatch, getState, extra }) => {
    // 
    // const res = await axios.get('http://123.207.32.32:8000/home/multidata')
    // //  extraReducers  action.payload 
    // return res.data.data.banner.list.length
  },
  {} // 
);

//  slice slice  reducer  actions
export default createSlice({
  // slice action.type 
  name: "demo",
  //state 
  initialState: {
    num: 1,
  },
  //  reducer 
  reducers: {
    /**
     * 
     * @produce  reducer  case 
     * @produce  dispatch  Action Action  Reducer  Reducer  State
     */
    addNum: (state, { type, payload }) => {
      state.num += payload; //  State state
    },
    // 
    cutNum: (state, { type, payload }) => {
      state.num -= payload;
    },
  },
  /**
   *  slice  Action createAsyncThunk  Action slice  Action
   */
  extraReducers: builder => {
    const { pending, fulfilled, rejected } = getAsyncCountAction;
    builder
      .addCase(pending, (state, { payload }) => {
        console.log("pending--------------");
      })
      .addCase(rejected, (state, { payload }) => {
        console.log("rejected--------------");
      })
      .addCase(fulfilled, (state, { payload }) => {
        console.log("fulfilled--------------");
        // state.num = payload;
      });
  },
  selectors: {
    selectItems: state => state.num,
  },
});
